import uniRequest from 'uni-request'
export default {
	data() {
		return {
			source: 0,
			addressList: [],
		}
	},
	onLoad(option){
		console.log(option.source);
		this.source = option.source;
	},
	onShow(){
		this.getAddress()
	},
	methods: {
		async getAddress(data,type){
			console.log(data)
			const response = await uniRequest({
				url:'/addresses/',
				method:'get',
				headers:{
					Authorization:'JWT '+uni.getStorageSync('userInfo').token
				},
			}).then(response=>{
				console.log(response.data)
				this.addressList = response.data.addresses
				if(type === "add"){
					this.addressList.push(data);
				}
				this.addressList.forEach((ele,index)=>{
					if(ele.id === response.data.default_address_id){
						ele.default_address_id = true
					}
					if(ele.addressName !== null){
						if(data !== undefined){
							if(ele.id === data.id){
								ele.addressName = data.addressName
							}
						}
					}else{
						ele.addressName = ele.province + ' ' + ele.city+ ' ' + ele.district+ ' '
					}
				})
				
			}).catch(error=>{
				console.log(error)
			})
		},
		//选择地址
		checkAddress(item){
			if(this.source == 1){
				//this.$api.prePage()获取上一页实例，在App.vue定义
				this.$api.prePage().addressData = item;
				uni.navigateBack()
			}
		},
		addAddress(type, item){
			console.log(item)
			uni.navigateTo({
				url: `/pages/address/addressManage?type=${type}&data=${JSON.stringify(item)}`
			})
		},
		
		
		//添加或修改成功之后回调
		refreshList(data, type){
			console.log(data, type);
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			if(type === 'add'){
				this.getAddress(data,type)
			}else{
				this.getAddress(data,type)
			}
		}
	}
}
